import React from 'react';

import AppContainer from "../common/AppContainer/AppContainer";
import Step from "./Step";
import s1h1Img from '../../assets/Frame27/s1h1.jpg';
import s1h2Img from '../../assets/Frame27/s1h2.jpg';
import s2h1Img from '../../assets/Frame27/s2h1.jpg';
import s2h2Img from '../../assets/Frame27/s2h2.jpg';
import s3h1Img from '../../assets/Frame27/s3h1.jpg';
import s3h2Img from '../../assets/Frame27/s3h2.jpg';
import AppContainerItem from "../common/AppContainer/AppContainerItem";
import {Typography} from "@material-ui/core";


const DesigningSteps = ({grey}) => {
    const text1 = ' 3D-,     .      ,    ';
    const listTitle1 = '      :';

    const text2 = '   ,          ,    ,  ,      ,        .';
    const listTitle2 = '     :';

    const text3 = '   ,      ,    ,    ';
    const listTitle3 = '     :';

    return (
        <AppContainer title={'  '} grey={!!grey}>
            <Step step={1} text={text1} listTitle={listTitle1} image1={s1h1Img} image2={s1h2Img}>
                {[
                    '  ',
                    '  ',
                    '',
                    '    ',
                    '   ',
                ]}
            </Step>
            <Step step={2} text={text2} listTitle={listTitle2} image1={s2h1Img} image2={s2h2Img}>
                {[
                    '  ',
                    '  ',
                    ' ',
                    '           ',
                    '    ',
                    '        , ',
                    '      ',
                    '     .    ',
                    ' ',
                    '     ',
                ]}
            </Step>
            <Step step={3} text={text3} listTitle={listTitle3} image1={s3h1Img} image2={s3h2Img} withoutMargin={true}>
                {[
                    '  (//)',
                    '   ,   ()',
                    '  , , ',
                    '         ',
                    '       ',
                    '     ',
                    '   ',
                    '  ,   ()',
                    '  , , ',
                    '      ',
                ]}
            </Step>
            <AppContainerItem lg={12} md={12} sm={9} xs={12}>
                <Typography style={{color: '#00000099', textAlign: 'center'}}>
                        :     ,      ,          .
                </Typography>
            </AppContainerItem>
        </AppContainer>
    );
};

export default DesigningSteps;
